							endTime:endTime
						}
						console.log(arr)
							if(arr.startTime != '' && arr.startTime != null){
								this.clockItem.push(arr)
							}
						}
			},
    // 接口导出Excel表格
    exportExcel () {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
