    //       sums[index] = `实际出勤人次：${this.ountAttendancec}`
    //     } else if (index == 3) {
    //       sums[index] = `出勤率：${this.tatisticalAttendances * 100}%`
    //     }
    //   })

    //   return sums
    // }
  },
  computed: {
    tableContentValue () {
      return (row, value) => {
        var result = ''
        if (value == 'workPhoto') {
					if(row.workPhoto == null){
						result = '无'
					}else{
						result = `<img slot="reference" src=${row.workPhoto} style="width: 30px;height: 30px">`
					}
				}else if(value == 'type'){
					if(row[value] == 1 || row[value] == 2){
						result = '已打卡'
					}else{
						result = '未打卡'
					}
				}else if(value == 'endDate'){
					if(row[value] == 'null null'){
						result = ''
					}else{
						var endDateArr = row[value].split(" ")
						if(endDateArr[1] == "null"){
							result = endDateArr[0]
						}else{
							result = row[value]
						}
					}
				}else if(value == 'startDate'){
					if(row[value] == 'null null'){
						result = ''
					}else{
							var startDateArr = row[value].split(" ")
							if(startDateArr[1] == "null"){
								result = startDateArr[0]
							}else{
								result = row[value]
							}
					}
        }else {
          result = row[value]
				}
				// console.log(result)
        return result
      }
    },

    tableSortable () {
      return (value) => {
        var result = ''
        if (
          value == 'runningAmount' ||
          value == 'comprehensiveCost' ||
          value == 'grossProfit' ||
          value == 'robotGrossProfit' ||
          value == 'netProfit'
        ) {
          result = 'custom'
        } else {
          result = false
        }

        return result
      }
    }
  }
}
</script>

